.DrawerToggle {
  width: 40px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: left;
      align-items: left;
  padding: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}

.DrawerToggle div {
  width: 90%;
  height: 3px;
  background: -webkit-linear-gradient(
    305deg,
    #ffd443,
    #e2b239
  );
  background: -o-linear-gradient(
    305deg,
    #ffd443,
    #e2b239
  );
  background: linear-gradient(
    145deg,
    #ffd443,
    #e2b239
  );
  -webkit-box-shadow: 20px 20px 60px #d5a836,
    -20px -20px 60px #ffe448;
          box-shadow: 20px 20px 60px #d5a836,
    -20px -20px 60px #ffe448;
}

.DrawerToggle div:nth-child(2) {
  width: 60%;
  height: 3px;
  background-color: #fbc63f;
}

@media (min-width: 500px) {
  .DrawerToggle {
    display: none;
  }
}



